.header {
  position: fixed;
  right: 0;
  margin-top: 10px;
  z-index: 1;
  // width: 100%;

  &__menu{
    color: rgb(255, 255, 255);
  }

  &__list {
    list-style: none;
    display: flex;
  }

  &__list li {
    width: 140px;
    text-align: center;
    height: 50px;
    line-height: 50px;
    margin-right: 2px;
  }

  &__list li a {
    text-decoration: none;
    color: rgb(26, 26, 26);
    font-weight: bold;
    padding: 20px;
  }

}
